import React from 'react'
import VerificationInput from '../VerificationInput'
import './../../scss/modal.scss'


function VerifyCode({verifyCodeActive, VerifyCodeOpen}) {

    const verifyCode = (event) => {
        event.preventDefault();
    }

    return (
        <div className={verifyCodeActive ? 'modal-bg' : 'modal-bg--active'} onClick={VerifyCodeOpen}>
            <section className='modalBanner' onClick={(e) => {
                e.stopPropagation();
            }}>
                <div className="modalContainer">
                    <h1 className='modalTitle'>Verify Code</h1>
                    <form className='modalForm' onSubmit={(e) => verifyCode(e)}>
<VerificationInput/>
                        <button type='submit' className='modalSubmit'>Verify</button>


                    </form>
                </div>
            </section>
        </div>
    )
}

export default VerifyCode