import React, {useState} from 'react'
import './../../scss/modal.scss'


function PhoneVerification({PhoneVerificationActive, PhoneVerificationOpen, PhoneVerificationCodeOpen}) {

    const [email, setEmail] = useState("")


    const phoneVerificatioN = (event) => {
        event.preventDefault();
        PhoneVerificationOpen()
    }

    return (
        <div className={PhoneVerificationActive ? 'modal-bg' : 'modal-bg--active'} onClick={PhoneVerificationOpen}>
            <section className='modalBanner login' onClick={(e) => {
                e.stopPropagation();
            }}>
                <div className="modalContainer">
                    <h1 className='modalTitle'>
                        Phone Number Verification
                    </h1>
                    <h2 className='modalSubTitle'>Enter your phone number <span>to Login/Sign up</span></h2>
                    <form className='modalForm' onSubmit={(e) => phoneVerificatioN(e)}>
                        <label>
                            <input className='modalForm__inp' type='number' placeholder='+91'
                                   onChange={e => setEmail(e.target.value)} value={email}/>
                        </label>
                        <button type='button' className='modalSubmit' onClick={PhoneVerificationCodeOpen}>
                            Sign Up
                        </button>
                    </form>
                </div>
            </section>
        </div>
    )
}

export default PhoneVerification