import React, {useState} from 'react'
import './../../scss/modal.scss'
import faceI from './../../assets/images/faceIcon.png'
import GoogleI from './../../assets/images/googleIcon.png'
import AppleI from './../../assets/images/appleIcon.png'
import SignUpCostumer from './component/SignUpCostumer'
import SignUpDriver from './component/SignUpDriver'

function SignUp({signUpActiv, loginOpen, SignUpOpen}) {

    const [userName, setUserName] = useState("")
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const [confirmPassword, setConfirmPassword] = useState("")
    let [tab, setTab] = useState(false)
    // const [checked, setChecked] = useState(false)
    // const handleClick = () => setChecked(!checked)


    /*registerAs={true}*/
    return (
        <div className={signUpActiv ? 'modal-bg' : 'modal-bg--active'} onClick={SignUpOpen}>
            <section className='login' onClick={(e) => {
                e.stopPropagation();
            }}>
                <h1 className='login__title'>Log In to <span className='login__title__logo'>Blaze</span></h1>
                <div className="registerAs_checkbox">
                    <div onClick={() => setTab(true)} className='setTab_content'>
                        <input type="radio" name="setTabVal" checked={tab}/>
                        Costumer
                    </div>
                    <div onClick={() => setTab(false)} className='setTab_content'>
                        <input type="radio" name="setTabVal" checked={!tab}/>
                        Driver
                    </div>
                </div>
                <button className='login__continue__btn--f'><img src={faceI} className='login__continue__btn__img'/>Continue
                    with Facebook
                </button>
                <button className='login__continue__btn'><img src={GoogleI} className='login__continue__btn__img'/>Continue
                    with Google
                </button>
                <button className='login__continue__btn'><img src={AppleI} className='login__continue__btn__img'/>Continue
                    with Apple
                </button>
                <h2 className='login__title__OR'>OR</h2>

                {tab ?
                    <SignUpCostumer/>
                    :
                    <SignUpDriver/>
                }
                <p className='bottom__text'>Already have an account?<span className='bottom__text__link'
                                                                          onClick={loginOpen}>Log In</span>
                </p>

            </section>
        </div>
    )
}

export default SignUp