import React, {useState} from 'react'
import './../../scss/modal.scss'
import VerificationInput2 from "../VerificationInput2";


function PhoneVerificationCode({PhoneVerificationCodeActive,PhoneVerificationOpen, PhoneVerificationCodeOpen,}) {
    const [email, setEmail] = useState("")

    const phoneVerificationCode = (event) => {
        event.preventDefault();
        PhoneVerificationOpen();
    }

    return (
        <div className={PhoneVerificationCodeActive ? 'modal-bg' : 'modal-bg--active'} onClick={PhoneVerificationCodeOpen}>
            <section className='modalBanner login' onClick={(e) => {
                e.stopPropagation();
            }}>
                <div className="modalContainer">
                    <h1 className='modalTitle'>
                        Phone Number Verification
                    </h1>
                    <h2 className='modalSubTitle'>Enter 4 digit code sent to your phone <span>+91-5654644545</span></h2>
                    <form className='modalForm' onSubmit={(e) => phoneVerificationCode(e)}>
                        <VerificationInput2/>
                        <button type='button' className='modalSubmit'>
                            Next
                        </button>
                        <button type='submit' className='reSend_btn'>Resend Code</button>
                        <button type='submit' className='modalBack' onClick={PhoneVerificationCodeOpen}>
                            <span>&#8249;</span> Back
                        </button>
                    </form>

                </div>
            </section>
        </div>
    )
}

export default PhoneVerificationCode